@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.cookies {
    position: fixed;
    width: 350px;
    left: 16px;
    bottom: 16px;
    font-size: 16px;

    background-color: var(--bg);
    border: 1px solid var(--tertiary-accent);
    box-shadow: 0 0 8px 2px var(--shadow-color);

    @include notebook {
        width: auto;
        left: 8px;
        right: 8px;
        bottom: 8px;
    }

    &__content {
        padding: 16px;
        line-height: 1.2;
    }

    &__actions {
        display: flex;
        border-top: 1px solid var(--tertiary-accent);
    }

    &__action {
        flex: 1 0 0;
        padding: 12px;
        text-align: center;
        font-weight: 600;
        font-size: 16px;

        &:not(:last-child) {
            border-right: 1px solid var(--tertiary-accent);
        }

        &_danger {
            color: var(--text-error);
        }

        &_success {
            color: var(--text-success);
        }
    }
}
