@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables.scss";

.modal {

  &::v-deep {
    .vm--modal {
      background-color: var(--bg);
      border: 2px solid var(--lighter-primary);
    }

    .vm--overlay {
      background: rgba(0, 0, 0, var(--modal-overlay-opacity));
    }

    em {
      font-size: 0.9em;
      line-height: 1.2em;
      display: inline-block;
      margin-top: 4px;
    }
  }

  &_hide-page {
    &::v-deep {
      .vm--overlay {
        background: var(--bg);
      }
    }
  }
}

.text {
  padding: 15px 15px 10px;
  white-space: pre-line;
}

.buttons {
  display: flex;
  padding: 10px 15px 15px;
  border-top: 1px solid var(--tertiary-accent);

  button:last-child {
    margin-left: auto;
  }
}

.input {
  display: block;
  border: 1px solid var(--tertiary-accent);
  background-color: var(--tertiary-bg);
  color: var(--text-primary);
  border-radius: 2px;
  padding: 6px;
  font-size: 14px;
  margin-top: 5px;
  width: 100%;

  @include notebook {
    font-size: 16px;
  }
}
