@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables.scss";

.links {
  background-color: var(--footer-bottom-bg);
  padding: 25px 0;

  @include notebook {
    padding: 10px 0;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  &__item {
    margin-right: 40px;

    &:last-child {
      margin-right: 0;
    }

    @include notebook {
      margin-right: 0;
    }
  }

  &__link {
    color: $footer-link-color;
    font-size: 12px;
    font-weight: 500;

    @include notebook {
      line-height: 22px;
      padding: 0 10px;
    }
  }
}
