@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.nav {
  position: absolute;
  z-index: 5;
  top: 87px;
  left: 0;
  right: 0;
  background-image: linear-gradient(var(--navigation-bg), var(--navigation-bg));
  background-size: 100% 47px;
  background-repeat: no-repeat;
  scrollbar-width: none;
  -ms-overflow-style: none;

  @include notebook {
    width: 100vw;
    overflow: scroll;
    top: 61px;
  }

  &::-webkit-scrollbar {
    width: 0 !important;
    display: none !important;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }

  &__item {
    &:last-child {
      @include notebook {
        padding-right: 8px;
      }
    }

    @include notebook {
      margin-right: 15px;
    }
  }
}

.item {
  padding: 10px 0;
  position: relative;

  @mixin expand {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 4px;
    background-color: $primary;
  }

  &_expanded:before {
    @include expand;
  }

  &:hover:after {
    @include desktop-only {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -8px;
      height: 8px;
    }
  }

  &:hover:before {
    @include desktop-only {
      @include expand;
    }
  }

  &__heading {
    color: var(--text-tertiary);
    position: relative;
    display: flex;
    height: 27px;
  }

  &__label {
    margin-right: 8px;

    & + .item__icon {
      margin-left: -2px;
    }
  }

  &__text {
    display: flex;
    align-items: center;
    font-weight: 500;
    white-space: nowrap;
  }

  &__icon {
    margin-left: 5px;
    position: relative;
    top: 1px;
    width: 8px;
  }

  &__dropdown {
    display: none;
  }

  &_expanded .item__dropdown {
    display: block;
  }

  &:hover &__dropdown {
    @include desktop-only {
      display: block;
    }
  }
}

.label {
  white-space: nowrap;
  display: flex;
  font-weight: 600;
  padding: 5px 7px;
  background-color: var(--bg);
  border: 1px solid var(--tertiary-accent);
  border-radius: 3px;
  align-items: center;

  &__icon {
    filter: var(--img-filter);
  }

  &__icon + &__text {
    margin-left: 4px;
  }
}

.dropdown {
  position: absolute;
  top: 54px;
  left: -15px;
  border-radius: 4px;
  background-color: var(--bg);
  box-shadow: 0 2px 8px 0 var(--shadow-color);
  max-height: 400px;
  overflow-y: scroll;

  @include notebook {
    left: -5px;
    top: 52px;
    transition: box-shadow 0.1s;
  }

  &__link {
    display: block;
    white-space: nowrap;
    padding: 5px 15px;
    line-height: 20px;
    color: var(--text-primary);
    font-weight: 400;
    max-width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;

    &:hover {
      background-color: var(--lighter-primary);
    }
  }
}
