@import "./src/assets/themes/appdb/variables.scss";

@import "./assets/variables";

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  &:before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-image: var(--body-top-border-gradient);
  }

  &_search-expanded {
    @include notebook {
      &:after {
        content: "";
        display: block;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: 5;
      }
    }
  }
}

.content {
  padding-top: 87px;
  padding-bottom: 40px;

  @include notebook {
    padding-top: 67px;
  }
}
