@import "./src/assets/themes/appdb/variables.scss";

@import '@/assets/variables.scss';

.swiper {
    margin-top: -15px;
    margin-bottom: 28px;

    @include notebook {
        margin-top: 0;
        margin-bottom: 15px;
    }
}

.slide {
  max-width: 100%;
  max-height: 300px;
  object-fit: scale-down;
  display: block;
  margin: 0 auto;
}

.pagination {
    margin-top: 8px;
    text-align: center;

    &::v-deep &__bullet {
        display: inline-block;
        width: 12px;
        height: 12px;
        margin: 0 4px;
        border-radius: 100%;
        background-color: rgba(112,128,144,0.3);
        opacity: 1;

        &:focus {
            outline: none;
        }

        &_active {
            background-color: $text-secondary;
        }
    }
}
