@import "./src/assets/themes/appdb/variables.scss";

@import "../assets/variables";

.banner {
  position: relative;
  font-size: 20px;
  line-height: 32.5px;
  padding: 5px;
  text-align: center;
  background-color: var(--light-primary);

  &:not(:first-child) {
    border-top: 1px solid lighten($primary, 35);
  }

  @include notebook {
    font-size: 14px;
    line-height: initial;
  }

  &__inner {
    position: relative;
  }

  a {
    font-weight: 500;
    color: $primary;
  }
}
