@import "./src/assets/themes/appdb/variables.scss";

.description {
  background-color: var(--footer-top-bg);
  padding: 15px 0;
}

.list {
  display: flex;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;

  &__dropdown {
    position: absolute;
    bottom: calc(100% + 5px);
    background-color: $footer-top-bg;
    color: $footer-link-color;
    border: 1px solid $footer-button-border-color;
    border-radius: 3px;
    min-width: 230px;
    z-index: 5;

    &-item {
      padding: 5px;
      font-size: 12px;
      cursor: pointer;

      &:not(:last-child) {
        border-bottom: 1px solid $footer-button-border-color;
      }
    }
  }

  &__item {
    position: relative;
    margin-bottom: 4px;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  &__link {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 5px;
    border: 1px solid $footer-button-border-color;
    border-radius: 3px;
    color: $footer-link-color;
    font-size: 12px;
    font-weight: 500;
  }

  &__icon {
    margin-right: 4px;
  }

  &__desc {
    font-size: 10px;
    font-weight: 400;
    color: $footer-text-color;
    margin-left: 3px;
  }
}

.text {
  color: $footer-text-color;
  font-size: 12px;
  text-align: center;
  margin-top: 5px;
  line-height: 18px;

  &:not(:last-child) {
    margin-top: 10px;
  }
}

.link,
.text::v-deep a {
  color: $footer-link-color;
  font-size: 12px;
  font-weight: 500;
}
